.main-container{
    max-width: 980px;
    margin: 0 auto;
    padding: 50px 0;
    text-align: center;
}
.main-container ul{
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    margin-top: 50px;
}
.main-container ul li{
    display: flex;
    flex-direction: column;    
}
.main-container ul li img{
    max-width: 100%;
    border-radius: 5px 5px 0 0 ;
}

.main-container ul li footer{
    flex: 1;
    background-color: #fff;
    border: 1px solid #eee;
    padding: 15px 20px;
    text-align: left;  
    border-radius: 0 0 5px 5px;
}

.main-container ul li strong{
    font-size: 16px;
    color: #333;
}

.main-container ul li footer p{
    font-size: 14px;
    line-height: 20px;
    color: #999;
    margin-top: 5px;
}

.main-container ul li .button{
    margin-top: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
}

.main-container ul li .button button {
    height: 50px;
    box-shadow: 0 2px 2px rgba( 0, 0, 0, .5);
    border: 0;
    border-radius: 4px;
    background: #fff;
    cursor: pointer;
    transition: .2s all linear;
}

.main-container ul li .button button:hover{
    transform: translateY( -5px );
}

.main-container .empty{
    font-size: 32px;
    color: #999;
    font-weight: bold;
    margin-top: 200px;
}